
import Vue from "vue";

export default Vue.extend({
  computed: {
    isVisible(): boolean {
      const currRouteName: string = this.$route.name || "";
      if (currRouteName === "selectedArticle") return true;
      if (currRouteName === "selectedSoftware") return true;
      return false;
    },
  },
  methods: {
    goBack(): void {
      this.$router.go(-1);
    },
  },
});
