
import Vue from "vue";

export default Vue.extend({
  name: "FullNavItem",
  props: {
    route: {
      type: String,
      required: true,
    },
    text: {
      type: String,
      required: true,
    },
  },
  methods: {
    navigate(): void {
      if (this.route !== this.$route.path) {
        this.$router.push(this.route);
        this.$emit("clicked", this.$el);
      }
    },
  },
});
