
import Vue from "vue";

export default Vue.extend({
  name: "ProgressBar",
  props: {
    text: {
      type: String,
      required: true,
    },
    numer: {
      type: Number,
      required: true,
    },
    denom: {
      type: Number,
      required: true,
    },
    hidePercent: {
      type: Boolean,
      required: false,
    },
  },
  computed: {
    percentDone(): number {
      if (!this.numer) return 0;
      if (!this.denom) return 0;
      return Math.floor((this.numer / this.denom) * 100);
    },
  },
  methods: {
    setProgressBarFill() {
      const progressBarEl = this.$refs.progressBarFill as HTMLDivElement;
      const percentDone = Math.min(((this.numer || 0) / this.denom) * 100, 100);
      progressBarEl.style.width = `${percentDone}%`;
    },
  },
  mounted() {
    this.setProgressBarFill();
  },
});
