
import Vue from "vue";
import uiUtils from "@/utils/ui-utils";

export default Vue.extend({
  name: "SkeletonLoader",
  props: {
    imagePath: {
      type: String,
      required: true,
    },
    imageAlt: {
      type: String,
      required: true,
    },
    isCircle: {
      type: Boolean,
      required: false,
    },
  },
  data() {
    return {
      imageLoading: true,
    };
  },
  computed: {
    classObject(): Object {
      return {
        skeleton__loader: true,
        circle: this.$props.isCircle,
      };
    },
  },
  methods: {
    imageSource(): any {
      const result = uiUtils.loadImage(this.$props.imagePath);
      this.imageLoading = false;
      return result;
    },
  },
});
